<template>
    <!-- 導覽列 -->
    <div class="header">
        <div class="header__logo">
            <img src="@/assets/__black_float-logo-d.png" height="117" width="120" alt="logo-nav">
        </div>
        <div class="header__nav">
            <ul class="header__nav--list">
                <li @click="current = 1" class="header__nav--item">
                    <router-link :class="{'header__nav--active':current == 1}" to="/index">
                        關於煲閣
                        <div class="icon" :style="{width : iconSize + 'px',height : iconSize + 'px'}">
                            <aboutIcon :iconSize="iconSize"/>
                        </div>
                    </router-link> 
                </li>
                <li @click="current = 2" class="header__nav--item">
                    <router-link :class="{'header__nav--active':current == 2}" to="/food">
                        主餐菜單
                        <div class="icon" :style="{width : iconSize + 'px',height : iconSize + 'px'}">
                            <mainCourseIcon :iconSize="iconSize"/>
                        </div>
                    </router-link> 
                </li>
                <li @click="current = 5" class="header__nav--item">
                    <router-link :class="{'header__nav--active':current == 5}" to="/info">
                        加盟資訊
                        <div class="icon" :style="{width : iconSize + 'px',height : iconSize + 'px'}">
                            <infoIcon :iconSize="iconSize"/>
                        </div>
                    </router-link> 
                </li>
            </ul>
        </div>
        <div class="header__nav--mobile">
            <div class="btn" @click="openNavPage">
                <div class="line1"></div>
                <div class="line2"></div>
                <div class="line3"></div>
            </div>
        </div>
    </div>
</template>
<script>
import aboutIcon from '@/components/header-item-about'
import mainCourseIcon from '@/components/header-item-mainCourse'
import drinkIcon from '@/components/header-item-drink'
import infoIcon from '@/components/header-item-info'

export default {
    name:'header',
    components:{
        mainCourseIcon,
        aboutIcon,
        infoIcon
    },
    data() {
        return {
            current:0,
            visable:false,
            iconSize:30
        }
    },
    watch: {
        $route(to,from){
            const navPage = document.querySelector('.nav-page-mobile');
            const navItem = document.querySelectorAll('.nav-page-mobile__item');
            const navLogo = document.querySelector('.header__logo img');

            navPage.style.animation = '.5s fadeOut';
            navPage.style.opacity = 0;
            navPage.style.visibility = 'hidden';
            navItem.forEach((item,index)=>{
                item.style.opacity = 0;
            })

            navLogo.style.opacity = 1;

            setTimeout(()=>{
                navPage.style.display = 'none';
                this.visable = false;
            },500)
        }
    },
    methods: {
        changeCurrent(num){
            this.current = num;
        },
        openNavPage(){
            const navPage = document.querySelector('.nav-page-mobile');
            const navItem = document.querySelectorAll('.nav-page-mobile__item');

            if(this.visable == false){
                navPage.style.display = 'block';
                navPage.style.animation = '1s fadein';
                navPage.style.opacity = 1;
                navPage.style.visibility = 'visible';
                navItem.forEach((item,index)=>{
                    item.style.animation = '1s listShowOut';
                    item.style.animationDelay = `${index * 0.15}s`;
                    setTimeout(()=>{
                        item.style.opacity = 1;
                    },1000 * index * 0.15)
                })
                this.visable = true;
            }else if(this.visable == true){
                navPage.style.animation = '.5s fadeOut';
                navPage.style.opacity = 0;
                navPage.style.visibility = 'hidden';
                navItem.forEach((item,index)=>{
                    item.style.opacity = 0;
                })
                setTimeout(()=>{
                    navPage.style.display = 'none';
                    this.visable = false;
                },500)
            }
        },
        Logotoggle(){
            const navLogo = document.querySelector('.header__logo img');
            if(this.$router.history.current.path == '/index'){
                if(window.scrollY <= window.innerHeight -100 ){
                    navLogo.style.animation = '.2s fadeOut';
                    navLogo.style.opacity = 0;
                }else{
                    navLogo.style.animation = '1s fadein';
                    navLogo.style.opacity = 1;
                }
            }
        },
        hoverFoodNav(){

        }
    },
    created() {
        setInterval(() => {
            this.Logotoggle();
        }, 100);
    },
}

</script>