<template>
  <div id="app" class="container">
    <NavPage/>
    <Header/>
    <!-- 大圖 -->
    <div class="media__container">
      <img class="media__logo" :class="{ 'd-none' : isIndex }" src="@/assets/white__media-logo.png" alt="">
      <img class="media" :class="'media__img--' + imgClass" :style="'background-image: url('+ imgSrc +')'">
    </div>

    <backgroundPotRight :class="'background__' + imgClass + '--pot-right'"/>
    <backgroundPotLeft :class="'background__' + imgClass + '--pot-left'"/>
    <backgroundChopsticks :class="'background__' + imgClass + '--chopsticks'"/>
    <backgroundSpone :class="'background__' + imgClass + '--spone'"/>
    <backgroundTemple :class="'background__' + imgClass + '--temple'"/>
    <div class="content">
      <router-view></router-view>
    </div>
    <Footer></Footer>
  </div>
</template>

<script>
import Header from '@/components/header'
import Footer from '@/components/footer'
import NavPage from '@/components/nav-page-mobile'
import backgroundPotRight from '@/components/background-pot-right'
import backgroundPotLeft from '@/components/background-pot-left'
import backgroundChopsticks from '@/components/background-chopsticks'
import backgroundSpone from '@/components/background-spone'
import backgroundTemple from '@/components/background-temple'

export default {
  name: 'app',
  components: {
    Header,
    Footer,
    NavPage,
    backgroundPotRight,
    backgroundPotLeft,
    backgroundChopsticks,
    backgroundSpone,
    backgroundTemple,
  },
  data() {
    return {
      imgClass:'media__img--index',
      imgSrc:require('@/assets/零食背景圖.png'),
      isIndex:true
    }
  },
  methods: {
    
  },
  watch:{
    $route (to, from){
      this.changeImg();
    }
  },
  methods: {
    changeImg(){
      switch(this.$router.history.current.path){
        case '/board':
          this.imgClass = 'board';
          this.imgSrc = require('@/assets/零食背景圖.png');
          break;
        case '/article':
          this.imgClass = 'article';
          this.imgSrc = require('@/assets/零食背景圖.png');
          break;
        case '/food':
          this.imgClass = 'food';
          this.imgSrc = require('@/assets/零食背景圖.png');
          break;
        case '/drink':
          this.imgClass = 'drink';
          this.imgSrc = require('@/assets/零食背景圖.png');
          break;
        case '/dessert':
          this.imgClass = 'drink';
          this.imgSrc = require('@/assets/零食背景圖.png');
          break;
        case '/info':
          this.imgClass = 'info';
          this.imgSrc = require('@/assets/零食背景圖.png');
          break;
        default:
          this.imgClass = 'index';
          this.imgSrc = require('@/assets/封面2.png');
          break;
      }
    }
  },
  created() {
    this.changeImg();
  }
}
</script>

<style lang="sass">
  @import '@/assets/scss/main.scss'
</style>
